.card{
    width: calc( 33.3% - 2.2%);
    min-height: 25em;
    height: auto;
    margin-right: 1%;
    margin-left: 1%;
    margin-bottom: 1em;
    background-color: #fff;
    box-shadow: 0 5px 20px 0 rgb(0 0 0 / 10%);
    border-radius: 5px;
    border: 1px solid rgba(0,168,255,.3);
    text-decoration: none;
    color: black;
}

.card > img{
    width: 100%;
    border-radius: 5px 5px 0 0;
}

.card-container{
    padding: 12px 24px;
    font-family: Raleway,sans-serif;
}

.card-container > h3{
    margin: 0;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 20px;
    font-weight: 600;
}

.author{
    margin: 10px 0;
    color: #0096ff;
    font-size: 15px;
    font-weight: 400;
    font-family: 'Roboto', sans-serif;
}

.card-container span{
    font-weight: 300;
    color: #000;
}

.description{
    opacity: .5;
    font-family: Raleway;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.43;
    letter-spacing: .35px;
    text-align: left;
    color: #000;
    margin: 10px 0;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
